<template>
	<view class="hot_main">		
		<view class="list" v-for="item in articleList" @click="handler_detail(item)">
			<text class="f18">平城区青矿家属楼停电望恢复</text>
			<view class="list_b">
				<text class="labels f10">投诉</text>
				<text class="f12 time">2020/12/31 21:52:48</text>
			</view>
		</view>
		<view v-if="articleList.length <= 0" class="nomore pt50"><uni-load-more :status="nomoreStatus"></uni-load-more></view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			articleList:[],
			params:{
				pageNum :1,
				count:10,
				wechatOpenId :''
			},
			nomoreStatus: 'nomore'
		};
	},
	onLoad(options) {
		this.handler_loadData();
	},
	/*E 初始数据加载*/
	onShow() {},
	//下拉刷新
	onPullDownRefresh() {
		this.articleList = [];
		this.params.pageNum = 1;
		this.handler_loadData();
		uni.stopPullDownRefresh();
	},
	//加载更多
	onReachBottom() {
		//阻止重复加载
		if (!this.showLoadMore) {
			this.showLoadMore = true;
			this.params.pageNum += 1;
			this.handler_loadData();
		}
	},
	methods: {
		/*S  加载初始数据 */
		handler_loadData() {
			uni.showLoading({
				title: '请耐心等待哦'
			});
			this.params.wechatOpenId  = uni.getStorageSync('openid')
			var _this = this;
			_this.$minApi.hotlinePage({ ..._this.params }).then(res => {
				if (res.code == 200) {
					uni.hideLoading();
					if (res.data.resultList.length > 0) {
						if (res.data.resultList.length < _this.params.count) {
							_this.showLoadMore = true;
							_this.loadStatus = 'nomore';
						} else {
							_this.showLoadMore = false;
						}
						_this.articleList = _this.articleList.concat(res.data.resultList);
					}else{
						_this.showLoadMore = true;
						_this.loadStatus = 'nomore';
					}
				} else {
					uni.showToast({
						title: res.message
					});
				}
			});
		},
		/* E 加载初始数据 */
		handler_detail(date){
			var index = 'my';
			var hotlineAppealId = date.hotlineAppealId
			uni.navigateTo({
				url:'detail?index=${index}&hotlineAppealId=${hotlineAppealId}'
			})
		}
	}
};
</script>

<style lang="scss">
.hot_main{
	height: calc(100vh);
	background-color: #FFFFFF;
	padding:0upx 32upx;
	.list{
		padding:20upx 0upx;
		border-bottom: 2upx solid #E6E6E6;
		.labels{
			color:#CA3C3E ;
			padding:4upx 12upx;
			background: #FFE7E7;
			border-radius: 4upx;
			display: inline-block;
			margin-right: 20upx;
		}
		.time{
			color:#939393 ;
		}
		.list_b{
			margin-top: 20upx;
		}
	}
}
</style>
